<template>
    <div class="deposit-detail-page">

            <div class="kind">
                <img src="@/assets/images/bnb_token.png" >
                <span>BND</span>
            </div>
            <div class="property">
                <div 
                    class="item"
                    v-for="item in propertyList"
                    :key="item.id">
                    <span class="num">{{item.id}}</span>
                    <span class="name">{{item.name}}</span>
                </div>
            </div>
            <p class="title">
                <span>我要质押</span>
                <button
                    v-for="(item) in days"
                    :key="item.id"
                    :class="['btn' , {'active': activeDayIndex === item.id}]"
                    @click="toggleDays(item)">
                    {{item.name}}
                </button>
            </p>
            <div class="input-area">
                <div class="balance">
                    <span class="info">余额：</span>
                    <span class="num">100 BND</span>
                </div>
                <div class="inputBox">
                    <img src="@/assets/images/bnb_token.png" alt="">
                    <input 
                        type="text"
                        minLength="1"
                        maxLength="79"
                        @input="inputEvent">
                    <button>MAX</button>
                </div>
            </div>
            <div class="receive">
                <p class="tit">您将获得：</p>
                <p class="num"><span>500</span> idBND</p>
            </div>
            <div class="warn">
                提示：存入后获得计息币，前往质押挖矿页面，质押您的计息币获取质押收益
            </div>
            <div class="submitBtns">
                <button class="enter">存入</button>
                <button class="cancal" @click="returnPage">返回</button>
            </div>
    </div>
</template>
<script>
export default {
    name:'depositDetail',
    data(){
        return {
            propertyList:[
                {id:1 , name:'质押币种'},
            ],
            days:[
                {id:1 , name:'30天'},
                {id:2 , name:'60天'},
                {id:3 , name:'90天'},
                {id:4 , name:'180天'},
            ],
            activeDayIndex:4

        }
    },
    methods:{
        inputEvent(e){
            this.$inputLimit(e , 6)
        },
        toggleDays(item) {
            this.activeDayIndex = item.id
        },
        returnPage(){
            this.$router.go(-1)
        }
    }
}
</script>
<style lang="scss" scoped>
    .deposit-detail-page {
        background-color: #fff;
        border-radius: .3rem;
        padding: .3rem;
        p{
            margin: 0;
        }
    }
    .kind {
        padding-bottom: .3rem;
        img{
            // height: .36rem;
            vertical-align: middle;
            position: relative;
            top: -3px;
            margin-right: .12rem;
        }
        span {
            font-size: .3rem;
            font-weight: 600;
            color: #1C1C1B;
        }
    }
    .property {
        padding-bottom: .58rem;
        .item {
            .num {
                display: inline-block;
                height: .26rem;
                line-height: .26rem;
                padding: 0 .06rem;
                font-size: 12px;
                border: 1px solid #002DFF;
                border-radius: 3px;
                color: #002DFF;
                margin-right: .21rem;
            }
            .name {
                font-size: .26rem;
                color: #002DFF;
            }
        }
    }
    .title {
        color: #1C1C1B;
        font-size: .3rem;
        font-weight: 600;
        padding-bottom: .58rem;
        .btn {
            height: .4rem;
            width: 1rem;
            line-height: .34rem;
            border: 1px solid #333333;
            color: #333333;
            background-color: transparent;
            border-radius: .2rem;
            margin-left: .1rem;
            vertical-align: middle;
            cursor: pointer;
            font-size: .24rem;
        }
        .btn.active {
            border-color: #0031FF;
            color: #002EFF;
        }
    }
    .input-area {
        .balance {
            font-size: .24rem;
            line-height: .24rem;
            padding-bottom: .16rem;
            text-align: right;
            .info{
                color: #999999;
            }
            .num {
                color: #333333;
            }
        }
        .inputBox {
            height: 1.2rem;
            background-color: rgba(0, 49, 255 , .06);
            border-radius: .1rem;
            position: relative;
            img {
                position: absolute;
                height: .52rem;
                width: .52rem;
                left: .3rem;
                top: calc(50% - .26rem);
                
            }
            button{
                position: absolute;
                height: .6rem;
                width: 1.2rem;
                text-align: center;
                line-height: .52rem;
                border: 1px solid #0031FF;
                color: #002EFF;
                background-color: transparent;
                right: .3rem;
                 top: calc(50% - .3rem);
                 border-radius: .3rem;
                 cursor: pointer;
            }
            input {
                width: 100%;
                height: 1.2rem;
                padding: 0;
                margin: 0;
                border: none;
                outline: none;
                background-color: transparent;
                font-size: .26rem;
                color: #333333;
                padding: 0 1.5rem 0 .88rem;
                box-sizing: border-box;
            }
        }
    }
    .receive {
        padding-top: .6rem;
        .tit {
            color: #1C1C1B;
            font-size: .3rem;
            font-weight: 600;
        }
        .num {
            padding-top: .28rem;
            padding-bottom: .6rem;
            color: #232322;
            font-size: .26rem;
            span {
                font-weight: 600;
                color: #0024FF;
            }
        }
    }
    .warn {
        line-height: .4rem;
        padding:.2rem .3rem;
        background-color: rgba(255, 0, 0 , .06);
        font-size: .24rem;
        color: #EE1E1E;
        border-radius: .1rem;
    }
    .submitBtns {
        padding-top: .6rem;
        text-align: center;
        button {
            width: 2rem;
            height: .6rem;
            line-height: .52rem;
            border: 1px solid #0031FF;
            color: #002EFF;
            border-radius: .3rem;
            background-color: #fff;
            cursor: pointer;
        }
        .enter {
            background: linear-gradient(90deg, #0096FF, #0024FF);
            border: none;
            color: #fff;
            margin-right: .3rem;
        }
    }
</style>


